export function getInitializedCards() {
  return { playingCards: getInitializedPlayingCards };
}

export function getInitializedPlayingCards() {
  const allCardsIndices = [...Array(245).keys()];
  const allPlayerCardsIndices = allCardsIndices.slice(10, 245);
  return shuffle(allPlayerCardsIndices);
}

export function shuffle(array) {
  let currentIndex = array.length,
    randomIndex;

  // While there remain elements to shuffle.
  while (currentIndex !== 0) {
    // Pick a remaining element.
    randomIndex = Math.floor(Math.random() * currentIndex);
    currentIndex--;

    // And swap it with the current element.
    [array[currentIndex], array[randomIndex]] = [
      array[randomIndex],
      array[currentIndex],
    ];
  }

  return array;
}
